.pay-for {
  width: 3.75rem;
  padding: 0.34rem 0.16rem;
  box-sizing: border-box;
}
.pay-for .pay-money {
  width: 3.43rem;
  height: 1.5rem;
  border-radius: 0.12rem;
  background: url(../../static/media/money.4206754f.png) no-repeat left center;
  position: relative;
  margin-bottom: 0.3rem;
}
.pay-for .pay-money > a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.pay-for .pay-money .tit {
  height: 0.17rem;
  color: #ffffff;
  font-size: 0.12rem;
  left: 0.4rem;
  top: 0.2rem;
}
.pay-for .pay-money p {
  position: absolute;
  color: #fff;
}
.pay-for .pay-time {
  width: 3.43rem;
  height: 1.5rem;
  border-radius: 0.12rem;
  background: url(../../static/media/time.995e2b51.png) no-repeat left center;
  position: relative;
  margin-bottom: 0.3rem;
}
.pay-for .pay-time > a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.pay-for .pay-time .tit {
  height: 0.17rem;
  color: #e7e7e7;
  font-size: 0.12rem;
  left: 0.4rem;
  top: 0.2rem;
}
.pay-for .pay-time p {
  position: absolute;
  color: #fff;
}
.pay-for .pay-other {
  width: 3.43rem;
  height: 0.66rem;
  border-radius: 0.04rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.08rem 0.16rem 0rem rgba(114, 48, 0, 0.16);
  padding: 0.23rem;
  box-sizing: border-box;
}
.pay-for .pay-other img {
  height: 0.21rem;
  vertical-align: middle;
  margin-right: 0.24rem;
}
.pay-for .pay-other span {
  color: #333333;
  font-size: 0.15rem;
}
.pay-for .pay-other .right {
  color: #c7c7cc;
}
.pay-for p.money {
  left: 0.4rem;
  top: 0.55rem;
  line-height: 0.4rem;
  color: #ffffff;
  font-size: 0.14rem;
}
.pay-for p.money span {
  font-size: 0.3rem;
}
.pay-for p.time {
  line-height: 0.17rem;
  opacity: 0.8;
  color: #ffffff;
  font-size: 0.12rem;
  left: 0.4rem;
  bottom: 0.15rem;
}
.pay-for p.type {
  line-height: 0.17rem;
  opacity: 0.8;
  color: #ffffff;
  font-size: 0.12rem;
  right: 0.24rem;
  bottom: 0.15rem;
}
.pay-for p.go {
  font-size: 0.15rem;
  right: 0.24rem;
  bottom: 0.7rem;
  opacity: 0.8;
}
.pay-detail h3 {
  left: 0.29rem;
  height: 0.2rem;
  line-height: 0.2rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.13rem;
  text-indent: 0.13rem;
  border-left: 0.05rem solid #E9B551;
}
.pay-detail .detail-info {
  width: 3.75rem;
  padding: 0.16rem;
  box-sizing: border-box;
  border-bottom: 0.1rem solid #F5F5F5;
}
.pay-detail .detail-info p {
  line-height: 0.26rem;
  color: #333333;
  font-size: 0.15rem;
}
.pay-detail .detail-c {
  width: 3.45rem;
  padding: 0.15rem;
  box-sizing: border-box;
  border-bottom: none;
}
.pay-detail .detail-c .detail-c-i {
  padding: 0.15rem;
  border-bottom: 0.01rem solid #EEEEEE;
}
.pay-detail .detail-c .detail-c-i .tit {
  height: 0.22rem;
  color: #333333;
  font-size: 0.16rem;
  margin-bottom: 0.05rem;
}
.pay-detail .detail-c .detail-c-i .tips {
  line-height: 0.22rem;
  color: #999999;
  font-size: 0.14rem;
}
.pay-detail .detail-up {
  position: fixed;
  bottom: 0;
  height: 0.7rem;
  background: #fff;
  z-index: 15;
  width: 100%;
}
.pay-detail .detail-up .detail-up-btn {
  margin-left: 0.15rem;
  width: 3.43rem;
  height: 0.4rem;
  line-height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
}
.pay-success {
  width: 3.75rem;
  box-sizing: border-box;
}
.pay-success .success {
  text-align: center;
  padding: 0.6rem 0;
}
.pay-success .success img {
  width: 1.2rem;
  margin-bottom: 0.38rem;
}
.pay-success .success h4 {
  line-height: 0.25rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.16rem;
}
.pay-success .success p {
  color: #333333;
  font-size: 0.14rem;
  margin-bottom: 0.3rem;
  padding: 0 0.16rem;
}
.pay-success .success p span {
  color: #DA5757;
}
.pay-success .success a {
  width: 1.64rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  text-align: center;
  line-height: 0.4rem;
  color: #ffffff;
  font-size: 0.16rem;
}
